<script>

import {branches} from "@/model/learn-sim";
import {header} from "@/utils/DataTableUtil";

const branchesRows = [
  ['', '', '', '', '', ''],
  ['', '', '', '', '', '']
]

const attrs = [
    '', '', '', '', '', '', '', ''
]

const learnType = ['', '', '']
const bookType = [ '', '', '', '', '', '', '', '' ]

const learnHeaders = [
  header('', '', '', 'col', { align: 'start' }),
]

const bookHeaders = [
    header('', '', '', 'level', { align: 'start' }),
    header('', '', '', 'name', { sortable: false }),
    header('', '', '', 'skill', { sortable: false })
]

export default {
  name: "LearnSimulator",
  components: {

  },
  data() {
    return {
      config: {
        branches,
        attrs,
        learnType,
        bookType
      }
    }
  },
  computed: {
    branches() {
      const res = branchesRows.map((row, i) => {
        return {
          index: i,
          cols: row.map((name, j) => {
            return {
              index: j,
              name: name,
              list: branches[name]
            }
          })
        }
      })
      return res;
    },
    learnHeaders() {
      return learnHeaders;
    },
    bookHeaders() {
      return bookHeaders;
    }
  },
}
</script>


